
<template>
  <div>
    <Modal v-model="showModal" :mask-closable="false" width="500" @on-cancel="cancel">
      <h3 slot="header"></h3>
      <Form :model="formPassword" label-position="right" :label-width="100" ref="formPasswordValidate"
            :rules="ruleValidate">
        <FormItem label=":">
          <span>{{userName}}</span>
        </FormItem>
        <FormItem label=":" prop="oldPassword">
          <Input type="password" v-model="formPassword.oldPassword" placeholder=""/>
        </FormItem>
        <FormItem label=":" prop="newPassword">
          <Input type="password" v-model="formPassword.newPassword" placeholder=""/>
        </FormItem>
        <FormItem label=":" prop="confirmPassword">
          <Input type="password" v-model="formPassword.confirmPassword" placeholder=""/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="saveLoading" @click="save('formPasswordValidate')"></Button>
        <Button @click="cancel"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import md5 from 'md5';

export default {
  name: 'ownSapce',
  components: {
  },
  props: {
    isShowOwnSpace: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const validateOldPassword = (rule, value, callback) => {
      if (/^\s*$/g.test(value)) {
        callback(new Error(''));
      }
      /* if (!/^(?![0-9_!@#$%^&*()]+$)(?![a-zA-Z_!@#$%^&*()]+$)(?![_!@#$%^&*()]+$)[0-9A-Za-z_!@#$%^&*()]{6,16}$/g.test(value)) {
          callback(new Error("6-16"));
        } */
      callback();
    };
    const validateNewPassword = (rule, value, callback) => {
      if (/^\s*$/g.test(value)) {
        callback(new Error(''));
      }
      if (!/^(?![0-9_!@#$%^&*()]+$)(?![a-zA-Z_!@#$%^&*()]+$)(?![_!@#$%^&*()]+$)[0-9A-Za-z_!@#$%^&*()]{6,16}$/g.test(value)) {
        callback(new Error('6-16'));
      }
      if (this.formPassword.oldPassword === this.formPassword.newPassword) {
        callback(new Error(''));
      }
      callback();
    };

    const validateConfirmPassword = (rule, value, callback) => {
      if (/^\s*$/g.test(value)) {
        callback(new Error(''));
      }
      if (!/^(?![0-9_!@#$%^&*()]+$)(?![a-zA-Z_!@#$%^&*()]+$)(?![_!@#$%^&*()]+$)[0-9A-Za-z_!@#$%^&*()]{6,16}$/g.test(value)) {
        callback(new Error('6-16'));
      }
      if (this.formPassword.newPassword !== this.formPassword.confirmPassword) {
        callback(new Error(''));
      }
      callback();
    };

    return {
      saveLoading: false,
      showModal: false,
      formPassword: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      ruleValidate: {
        oldPassword: [
          { required: true, validator: validateOldPassword, trigger: 'blur' }
        ],
        newPassword: [
          { required: true, validator: validateNewPassword, trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, validator: validateConfirmPassword, trigger: 'blur' }
        ]
      }
    };
  },
  computed: {
    userName () {
      return this.$store.state.user.userName;
    }

  },
  methods: {
    /**
     * 
     * */
    resetLoading: function () {
      this.saveLoading = true;
      setTimeout(() => {
        this.saveLoading = false;
      }, 1500);
    },
    cancel: function () {
      console.log('lineNum:', '114', 'class:', 'cancel');
      this.resetFields();
      this.$emit('hideOwnSpace');
    },
    resetFields: function () {
      this.$refs['formPasswordValidate'].resetFields();
    },
    save: function (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          let params = {
            oldPassword: md5(this.formPassword.oldPassword),
            newPassword: md5(this.formPassword.newPassword)
          };
          this.$store.dispatch('user/editPassword', params);
        } else {
          this.$Message.error('');
          this.resetLoading();
        }
      });
    }
  },
  mounted () {
    this.showModal = this.isShowOwnSpace;
  },
  watch: {
    isShowOwnSpace (val) {
      this.showModal = val;
    }
  }
};
</script>
